.blog {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 30px 0;
  display: flex;
}

.blog__content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 28px;
  margin-top: 36px;
  display: flex;
}

@media (width >= 768px) {
  .blog__content {
    flex-flow: wrap;
    gap: 20px;
  }
}

@media (width >= 1200px) {
  .blog__content {
    gap: 30px;
    margin-top: 42px;
  }
}

.blog__card {
  flex-direction: column;
  justify-content: center;
  display: flex;
  position: relative;
}

.blog__card-media {
  color: #2d3033;
  align-items: center;
  gap: 14px;
  margin: 0;
  font-family: Raleway, sans-serif;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.blog__card-media-num {
  width: 44px;
}

@media (width >= 1200px) {
  .blog__card-media {
    gap: 16px;
    font-size: 13px;
  }

  .blog__card-media-svg {
    width: 32px;
    height: 32px;
  }

  .blog__card-media-num {
    width: 52px;
  }
}

.blog__card-svg {
  z-index: 5;
  width: 68px;
  height: 68px;
  position: absolute;
  top: 125px;
  bottom: 20px;
  left: -1px;
}

@media (width >= 768px) {
  .blog__card-svg {
    left: -6px;
  }
}

@media (width >= 1200px) {
  .blog__card-svg {
    width: 80px;
    height: 80px;
    left: -14px;
  }
}

.blog__card-title {
  text-transform: uppercase;
  align-items: center;
  height: 34px;
  margin: 26px 0 0;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 700;
  display: flex;
}

@media (width >= 1200px) {
  .blog__card-title {
    height: 40px;
    margin: 30px 0 0;
  }
}

.blog__card-text {
  vertical-align: middle;
  color: #2d3033;
  align-items: center;
  width: 288px;
  height: 100px;
  margin: 0 0 20px;
  padding: 0;
  font-family: Raleway, sans-serif;
  font-size: 14px;
  font-weight: 400;
  display: flex;
}

@media (width >= 1200px) {
  .blog__card-text {
    height: 120px;
    margin: 0 0 22px;
  }
}

.blog__card-img {
  width: 288px;
  height: 214px;
}

@media (width >= 768px) {
  .blog__card-img {
    width: 318px;
  }
}

@media (width >= 1200px) {
  .blog__card-img {
    width: 370px;
    height: 252px;
  }
}

.blog__card-media-svg {
  cursor: pointer;
}

.blog__card-media-svg:hover path {
  fill: #2d3033;
}

.blog__banner {
  box-sizing: border-box;
  background-image: url("bcg_spot.bf209e25.png");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  align-items: center;
  padding: 42px 0;
  display: flex;
  position: relative;
}

.blog__banner-svg {
  z-index: 5;
}

.blog__banner .blog__banner-subtitle {
  color: #f38281;
  z-index: 5;
  height: 80px;
  margin-bottom: 24px;
}

.blog__banner-separator {
  z-index: 5;
}

@media (width >= 768px) {
  .blog__banner {
    background-image: url("bcg-spot.3006ee1b.png");
    background-position: center;
    height: 156px;
    padding: 28px 0;
  }

  .blog__banner-subtitle {
    height: 54px !important;
    margin-bottom: 16px !important;
  }

  .blog__banner-svg {
    width: 28px;
    height: 28px;
  }

  .blog__banner-separator {
    width: 148px;
  }
}

@media (width >= 1200px) {
  .blog__banner {
    height: 230px;
    padding: 42px 0;
  }

  .blog__banner-subtitle {
    height: 80px !important;
    margin-bottom: 24px !important;
  }

  .blog__banner-svg {
    width: 42px;
    height: 42px;
  }

  .blog__banner-separator {
    width: 170px;
  }
}

.blog__banner:after {
  content: " ";
  background-color: #ffffffe5;
  position: absolute;
  inset: 0;
}
